// js/tone-static/components/Player.tsx
"use client";

import { useState, useRef } from "react";
import { searchTracks, Track } from "../lib/jamendo";
import { DoubleLinkedList, Node } from "../lib/DoubleLinkedList";
import "../app/globals.css";

// ================= Utils =================
const formatTime = (timeInSeconds: number): string => {
  if (isNaN(timeInSeconds)) return "0:00";
  const minutes = Math.floor(timeInSeconds / 60);
  const seconds = Math.floor(timeInSeconds % 60);
  return `${minutes}:${seconds < 10 ? "0" : ""}${seconds}`;
};

// Funci√≥n para obtener duraci√≥n de un track
async function getTrackDuration(url: string): Promise<number> {
  return new Promise((resolve) => {
    const audio = new Audio(url);
    audio.addEventListener("loadedmetadata", () => resolve(audio.duration));
  });
}

export default function Player() {
  const [query, setQuery] = useState("");
  const [tracks, setTracks] = useState<(Track & { duration: number })[]>([]);
  const [playlist, setPlaylist] = useState<(Track & { duration: number })[]>([]);
  const [currentNode, setCurrentNode] = useState<Node<Track & { duration: number }> | null>(null);
  const [loop, setLoop] = useState(false);
  const [isPlaying, setIsPlaying] = useState(false);
  const [progress, setProgress] = useState(0);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const [loadingSearch, setLoadingSearch] = useState(false);

  const audioRef = useRef<HTMLAudioElement | null>(null);
  const playlistRef = useRef(new DoubleLinkedList<Track & { duration: number }>());
  const [dragIndex, setDragIndex] = useState<number | null>(null);

  // ================= Funciones =================
  async function handleSearch(e: React.FormEvent) {
    e.preventDefault();
    setLoadingSearch(true);
    const results = await searchTracks(query);

    const resultsWithDuration = await Promise.all(
      results.map(async (track) => {
        const duration = track.audio ? await getTrackDuration(track.audio) : 0;
        return { ...track, duration };
      })
    );

    setTracks(resultsWithDuration);
    setLoadingSearch(false);
  }

  function syncPlaylist() {
    setPlaylist(playlistRef.current.toArray());
  }

  function addToPlaylist(track: Track & { duration: number }, autoPlay = false) {
    let exists = false;
    let node = playlistRef.current.head;
    while (node) {
      if (node.value.id === track.id) {
        exists = true;
        break;
      }
      node = node.next;
    }
    if (exists) return;

    const newNode = playlistRef.current.append(track);
    syncPlaylist();

    if (!currentNode || autoPlay) playNode(newNode);
    return newNode;
  }

  function playNode(node: Node<Track & { duration: number }>) {
    if (!node) return;
    setCurrentNode(node);
    if (!audioRef.current) audioRef.current = new Audio();
    const audioEl = audioRef.current;

    audioEl.src = node.value.audio;
    audioEl.loop = loop;
    audioEl.play().then(() => setIsPlaying(true)).catch(console.error);

    audioEl.onended = () => {
      if (loop) playNode(node);
      else if (node.next) playNode(node.next);
      else setIsPlaying(false);
    };
  }

  function togglePlayPause() {
    if (!audioRef.current || !currentNode) return;
    if (isPlaying) {
      audioRef.current.pause();
      setIsPlaying(false);
    } else {
      audioRef.current.play();
      setIsPlaying(true);
    }
  }

  function stopSong() {
    if (audioRef.current) {
      audioRef.current.pause();
      audioRef.current.currentTime = 0;
      setIsPlaying(false);
      setProgress(0);
      setCurrentTime(0);
    }
  }

  function playPrev() {
    if (currentNode?.prev) playNode(currentNode.prev);
  }

  function playNext() {
    if (currentNode?.next) playNode(currentNode.next);
  }

  function updateProgress() {
    if (audioRef.current) {
      setProgress((audioRef.current.currentTime / audioRef.current.duration) * 100);
      setCurrentTime(audioRef.current.currentTime);
      setDuration(audioRef.current.duration);
    }
  }

  function seek(e: React.MouseEvent<HTMLDivElement>) {
    if (!audioRef.current) return;
    const rect = e.currentTarget.getBoundingClientRect();
    const percent = (e.clientX - rect.left) / rect.width;
    audioRef.current.currentTime = percent * audioRef.current.duration;
  }

  function handleFileUpload(e: React.ChangeEvent<HTMLInputElement>) {
    const file = e.target.files?.[0];
    if (file) {
      const url = URL.createObjectURL(file);
      getTrackDuration(url).then((duration) => {
        const localTrack: Track & { duration: number } = {
          id: file.name,
          name: file.name,
          artist_name: "Local",
          audio: url,
          duration,
        };
        addToPlaylist(localTrack, true);
      });
    }
  }

  function removeFromPlaylist(id: string) {
    let node = playlistRef.current.head;
    while (node) {
      if (node.value.id === id) {
        if (currentNode?.value.id === id) stopSong();
        playlistRef.current.removeNode(node);
        syncPlaylist();
        break;
      }
      node = node.next;
    }
  }

  // ================= Drag & Drop =================
  function handleDragStart(e: React.DragEvent<HTMLLIElement>, index: number) {
    setDragIndex(index);
    e.dataTransfer.setData("text/plain", index.toString());
  }

  function handleDragOver(e: React.DragEvent<HTMLLIElement>) {
    e.preventDefault();
  }

  function handleDrop(e: React.DragEvent<HTMLLIElement>, dropIndex: number) {
    e.preventDefault();
    const data = e.dataTransfer.getData("text/plain");
    const fromIndex = data ? parseInt(data, 10) : (dragIndex ?? -1);

    if (fromIndex < 0 || fromIndex === dropIndex || isNaN(fromIndex)) {
      setDragIndex(null);
      return;
    }

    playlistRef.current.move(fromIndex, dropIndex);
    syncPlaylist();

    const currentId = currentNode?.value.id;
    if (currentId) {
      let node = playlistRef.current.head;
      while (node && node.value.id !== currentId) {
        node = node.next;
      }
      setCurrentNode(node ?? null);
    }

    setDragIndex(null);
  }

  // ================= UI =================
  return (
    <div className="player-container">
      <h1>Tone Static</h1>

      <form onSubmit={handleSearch} className="search-form">
        {loadingSearch && (
          <div className="sound-spectrum" aria-hidden="true" style={{ marginRight: 8 }}>
            {Array.from({ length: 12 }).map((_, i) => (
              <span key={i} className="bar"></span>
            ))}
          </div>
        )}

        <input
          type="text"
          placeholder="Buscar en Jamendo..."
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          className="w-full p-8 pr-100" // üî• pr-12 deja espacio para el espectro
        />
        <button type="submit" disabled={loadingSearch}>
          Buscar
        </button>
      </form>

      <div className="lists-container">
        {/* Resultados */}
        <div className="results">
          <h3>Resultados</h3>
          <ul>
            {tracks.map((track) => (
              <li key={track.id}>
                <span className="track-info">
                  <strong>{track.name}</strong> -- {track.artist_name}{" "}
                  {track.duration && <span>({formatTime(track.duration)})</span>}
                </span>
                <div className="opt">
                  <button onClick={() => addToPlaylist(track, true)}>‚ñ∂ Reproducir</button>
                  <button onClick={() => addToPlaylist(track)}>+ A√±adir</button>
                </div>
              </li>
            ))}
          </ul>
        </div>

        {/* Playlist */}
        <div className="playlist">
          <h3>Lista</h3>
          <ul>
            {playlist.length > 0 ? (
              playlist.map((track, index) => {
                const isCurrent = currentNode?.value.id === track.id;
                return (
                  <li
                    key={track.id}
                    className={isCurrent ? "active" : ""}
                    draggable
                    onDragStart={(e) => handleDragStart(e, index)}
                    onDragOver={handleDragOver}
                    onDrop={(e) => handleDrop(e, index)}
                  >
                    <span className="track-info">
                      {index + 1}. {track.name} -- {track.artist_name}{" "}
                      {track.duration && <span>({formatTime(track.duration)})</span>}
                    </span>

                    <div className="button-group">
                      <button
                        onClick={() => {
                          let node = playlistRef.current.head;
                          while (node && node.value.id !== track.id) node = node.next;
                          if (node) playNode(node);
                        }}
                      >
                        {isCurrent ? "‚è∏ En reproducci√≥n" : "Reproducir"}
                      </button>
                      <button onClick={() => removeFromPlaylist(track.id)}>Eliminar</button>
                    </div>
                  </li>
                );
              })
            ) : (
              <p>No hay canciones en la lista.</p>
            )}
          </ul>
        </div>
      </div>

      {/* Barra de reproducci√≥n */}
      <div className="player-bar">
        <div className="player-content">
          <p>
            {currentNode ? (
              <>
                <strong>{currentNode.value.name}</strong> -- {currentNode.value.artist_name}
              </>
            ) : (
              "Ninguna canci√≥n seleccionada"
            )}
          </p>

          <div className="progress-container" onClick={seek}>
            <div className="progress" style={{ width: `${progress}%` }}></div>
          </div>

          <div className="time">
            <span>{formatTime(currentTime)}</span> / <span>{formatTime(duration)}</span>
          </div>

          <div className="controls">
            <div className="upload">
              <label className="upload-btn">
                Subir canci√≥n
                <input
                  type="file"
                  accept="audio/*"
                  onChange={handleFileUpload}
                  style={{ display: "none" }}
                />
              </label>
            </div>
            <button onClick={playPrev} disabled={!currentNode?.prev}>‚èÆ</button>
            <button onClick={togglePlayPause} disabled={!currentNode}>
              {isPlaying ? "‚è∏" : "‚ñ∂"}
            </button>
            <button onClick={stopSong} disabled={!currentNode}>‚èπ</button>
            <button onClick={playNext} disabled={!currentNode?.next}>‚è≠</button>
            <button onClick={() => setLoop(!loop)}>{loop ? "Loop ON" : "Loop OFF"}</button>
          </div>

          <audio ref={audioRef} onTimeUpdate={updateProgress} style={{ display: "none" }} />
        </div>
      </div>
    </div>
  );
}

